<template>
  <div>


     <el-drawer title="选择应用到的计划" :visible.sync="drawer" :direction="direction" size="50%">
                 <div style="margin:20px;">
                     <h3>计划列表</h3>
                     
                         <el-alert
                           :title="'含关键词“'+CampaignScheduleInput+'”的计划:'"
                           type="warning" v-if="isfilter">
                         </el-alert>
     
                     <el-checkbox-group v-model="checkedList" style="margin: 20px; max-height:400px; width:80%;overflow-y: auto">
                         <el-row v-for="item in CampaignScheduleShow">
                             <el-col>
                                 <el-checkbox :label="item.campaign_id">{{item.title}}</el-checkbox>
                             </el-col>
                         </el-row>
                     </el-checkbox-group>
                     <el-row>
                         <el-col><el-checkbox v-model="CampaignScheduleCheckAll" @change="handleCheckAllCampaignSchedule">全选</el-checkbox></el-col>
                         <el-col>
                             
                             <el-input v-model="CampaignScheduleInput" placeholder="关键词" @input="ListFilter" clearable size="small">
                                 <template slot="prepend">关键词搜索：</template>
                             </el-input>
                         </el-col>
                     </el-row>
                       
                     <el-button @click="Apply" type="primary" :loading="applyLoading" style="margin-top: 10px;">立即应用</el-button>
     
                 </div>
             </el-drawer>
      


  </div>



</template>

<script>

  export default {
    
    props: {
      act_index: {
        type:Number
      },
    },


    data() {
        return {
            drawer: false,
            direction: 'rtl',
             checkedList: [],
            CampaignSchedule: [],
            CampaignScheduleShow: [],
            CampaignScheduleCheckAll:false,
            CampaignScheduleInput:'',
            isfilter:false,
            applyLoading:false
        }

    },

    methods: {
        
        handleCheckAllCampaignSchedule() {
            if(this.CampaignScheduleCheckAll )
            {
                for(var x in this.CampaignScheduleShow)
                {
                    this.checkedList.push( this.CampaignScheduleShow[x].campaign_id);
                }
            } else {
                this.checkedList = [];                    
            }
        
        },
        ListFilter()
        {
            console.log(this.CampaignScheduleInput)
            if(this.CampaignScheduleInput.length>0)
            {
                this.isfilter = true;
                this.CampaignScheduleShow = [];
                for(var x in this.CampaignSchedule)
                {
                    if(this.CampaignSchedule[x].title.search(this.CampaignScheduleInput) != -1 )
                    this.CampaignScheduleShow.push( this.CampaignSchedule[x]);
                }
            } else {
                this.isfilter = false;
                this.CampaignScheduleShow = this.CampaignSchedule;
            }
        },  
        Apply() {
            if(this.checkedList.length<1)
            {
                this.$message({
                  type: 'warning',
                  message: '请选择应用到的计划!'
                });
                return;
            }
            
            
            
            this.applyLoading = true;
            this.$emit('applyScheduleList',this.checkedList );
        },

    },
    mounted() {



    }
  }
</script>

<style scoped>
  .el-row {
    line-height: 1.5rem;
  }
  ..el-checkbox-group {
      font-size: 1rem;
  }
</style>
