<template>
    <div>
        <h3>批量应用地域模板</h3>
        <div v-if="tplList.length<=0">
            尚未创建模板， <router-link to="/bat/areaCreate">点此创建。</router-link>
        </div>

        <div v-for="item in tplList" style="margin-bottom: 10px;">
            <el-card class="box-card" shadow="hover">

                <div slot="header" class="clearfix">
                    <span>模板：{{item.tpl_name}}</span>
                    <el-button style="float: right; padding: 3px 0" type="text" @click="deleteTpl(item)"><i class="el-icon-delete"></i>
                    </el-button>
                </div>
                <div class="text item tpl_content" @click="editAreaItem(item)">
                    {{parseTplData(item.source_data)}} ... &gt;&gt;
                </div>

                <div class="text item">
                    <el-button type="primary" @click="clickApply(item)">应用到计划</el-button>
                    <el-button @click="editAreaItem(item)">编辑</el-button>

                </div>
            </el-card>
        </div>


        <ScheduleList ref="ScheduleListComponent" @applyScheduleList="applyScheduleList"></ScheduleList>
        

    </div>
</template>

<script>
    import {
        request
    } from "@/network/request.js";
    import Qs from 'qs';
    
    import ScheduleList from "@/components/commen/ScheduleList.vue";
    
    
    export default {
        data() {
            return {
                tplList: [],
                drawer: false,
                direction: 'rtl',                
                schedule: '',
            }
        },
        components: {
            ScheduleList: ScheduleList,
        },
        methods: {
              
            getAreaTpl() {

                request({
                    url: "/subway/getareaTpl",
                    method: 'get',
                }).then(res => {
                    console.log(res)
                    this.tplList = res.data;
                })
            },
            getCampaignSchedule() {

                request({
                    url: "/subway/getCampaignSchedule",
                    method: 'get',
                }).then(res => {
                    console.log(res.data)

                    console.log(typeof res.data)
                    if (typeof res.data == "object") {
                       this.$refs.ScheduleListComponent.CampaignSchedule =res.data;
                       this.$refs.ScheduleListComponent.CampaignScheduleShow =res.data;
                    }

                })


            },
            deleteTpl(item) {

                this.$confirm('是否继续删除?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消'
                }).then(() => {

                    request({
                        url: "/subway/delAreaTpl",
                        method: 'POST',
                        data: Qs.stringify({
                            id: item.id
                        }),
                    }).then(res => {
                        console.log(res)

                        //去掉此条记录
                        this.getAreaTpl();

                    })
                    this.$message({
                        type: 'success',
                        message: '删除成功!'
                    });
                })

            },

            //打开计划选择框
            clickApply(item) {
                this.schedule = item.tpl_content;
                this.$refs.ScheduleListComponent.drawer = true;
            },
            //应用到计划
            applyScheduleList(checkedList) {

                //console.log(this.CampaignSchedule);

                for (var i = 0; i < checkedList.length; i++) {

                    request({
                        url: "/subway/updateCampaignArea",
                        method: 'POST',
                        data: Qs.stringify({
                            campaign_id: checkedList[i],
                            schedule: this.schedule,
                        }),
                    }).then(res => {
                        console.log(res)
                        if (typeof res.data.data.campaign_area.campaign_id != 'undefined') {
                            let cid = res.data.data.campaign_area.campaign_id;
                            this.$message({
                                type: 'success',
                                message: cid + '设置成功!'
                            });
                        } else {

                            this.$message({
                                type: 'fail',
                                message: '设置失败!'
                            });
                        }
                        
                        
                        this.$refs.ScheduleListComponent.applyLoading = false;


                    })
                }
            },
            editAreaItem(item) {
                this.$router.push({
                    path: '/bat/areaCreate',
                    query: {
                        id: item.id
                    }
                })
            },
            parseTplData(data) {
                var area_name = '';

                try {
                    data = JSON.parse(data)
                    
                    if (typeof data == "object") {
                        
                        
                        
                        for (var x in data) {
                            var area = data[x];
                        
                        
                        
                            for (var y in area) {
                                //console.log(area[y]);
                        
                                for (var z in area[y]) {
                                    if (area[y][z].checked == 'true' || area[y][z].checked == true) {
                                        area_name += area[y][z].name + " ";
                                    }
                                }
                        
                        
                        
                            }
                        }
                        return area_name;

                    }
                } catch (e) {
                    return data;
                    
                }

            }

        },
        mounted() {
            this.getAreaTpl();

            this.getCampaignSchedule();

        },
        watch: {
              '$route' (to, from) {
                  
                  if(from.path == '/bat/areaCreate')
                  {
                       this.getAreaTpl();
                       this.getCampaignSchedule();
                  }
        
              }
        },

    }
</script>

<style>
    .el-row {
        margin-bottom: 20px;

        &:last-child {
            margin-bottom: 0;
        }
    }

    .tpl_content {

        padding: 10px 0px;
        cursor: pointer;
    }
</style>
